<template>

  <section class="offerList">
    <div class="page-header">

      <div class="d-flex align-items-center">
        <b-button class="btn btn-gradient-info btn-rounded btn-icon" @click="backClick($event)">
          <i class="mdi mdi-arrow-left"></i>
        </b-button>
        <h3 class="page-title ml-3" style="font-weight: bold; font-size: 20px;" >
          Records
        </h3>
      </div>
<!--      <h3 class="page-title">-->
<!--&lt;!&ndash;        {{ this.name }}&ndash;&gt;-->
<!--      </h3>-->


<!--      <nav aria-label="breadcrumb">-->
<!--        <ol class="breadcrumb">-->
<!--          <li class="breadcrumb-item"><a href="javascript:void(0);">دسته بندی جدید</a></li>-->
<!--        </ol>-->
<!--      </nav>-->
    </div>
    <div class="row">

      <div class="col-lg-12 grid-margin stretch-card">
        <div class="card">
          <div class="card-body text-center pt-0 mt-0">

            <div class="d-flex flex-row justify-content-between align-items-center pt-0 mt-3">
              <h4 class="card-title pt-3 ">All Records</h4>

            </div>

            <div v-if="loading == true" class="text-secondary mt-3 mb-3" style="font-size: 14px;">
              <span
                  v-if="loading"
                  class="spinner-border spinner-border-sm mb-1 mt-1"
                  role="status"
                  aria-hidden="true"
              ></span>
            </div>
            <div v-else>
              <div v-if="items.length == 0" class="text-secondary mt-3 mb-3" style="font-size: 14px;">No Item</div>
              <table v-else class="table table-hover text-left mt-3">
                <thead>
                <tr>
                  <th scope="col">Meta Type</th>
                  <th scope="col">Date</th>
                </tr>
                </thead>
                <tbody>


                <tr v-for="(item, index) in items" style="cursor: pointer;"
                    :key="item._id" @click="itemClick($event,index)">
                  <td>{{ parseData(item.data) }}</td>
                  <td>{{ timestampToDate(item.time) }}</td>

                </tr>

                </tbody>
              </table>
            </div>

          </div>
        </div>
      </div>


    </div>
  </section>

</template>

<script>
import moment from 'moment'
// import utils from '../../util/utils'
import api from '../../const/api'

export default {
  name: 'offeryList',
  data () {
    return {
      id: null,
      name: null,
      loading: false,
      errored: false,
      errorMessage: "",
      success: false,
      items: [],
    }
  },
  mounted() {
    this.id = this.$route.params.id
    this.name = this.$route.params.name
    this.getRecords()
  },
  methods: {
    timestampToDate: function (timestamp) {
      let date = new Date(timestamp * 1000)
      return moment(date).format("YYYY-MM-DD hh:mm")
    },
    parseData: function (data) {
      const obj = JSON.parse(data);
      return obj.result.split('&')[0];
    },
    itemClick(e,index) {
      if (e.target.classList.length == 0) {

        var row = this.items[index]
        row.id
        this.$router.push({name:'records-detail',params: {
            id: row.id,
            data: row.data
          }})
      }
    },
    backClick: function () {
      this.$router.go(-1)
    },
    getRecords: function (){

      let formData = new FormData();
      formData.append('userid', this.id);

      this.loading = true;
      this.axios
          .post(api.baseURL + '/panel/records.php',formData, {
            headers: {
              Authorization: "Bearer " + localStorage.token
            }
          })
          .then(response => {

            // eslint-disable-next-line no-console
            console.log(response)
            this.loading = false
            if (response.status == 200) {
              this.items = response.data.list
            } else {
              this.errored = true
              this.errorMessage = response.data.message
            }

          })
          .catch(error => {
            // eslint-disable-next-line no-console
            console.log(error)
            this.loading = false
            this.errored = true
            this.errorMessage = 'Please try again'
          });

    },
  }
}
</script>